.loader {
  justify-content: center;
  display: flex;
}

.loader__container {
  height: 40px;
  align-items: center;
  display: flex;
  position: relative;
}

.loader__film {
  display: flex;
  position: absolute;
  top: -30px;
  left: 0;
}

.loader__film-img {
  animation: rotate 1.5s linear infinite;
}

@keyframes rotate {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

.spinner {
  z-index: 2;
  width: 50px;
  height: 50px;
  margin: -25px 0 0 -25px;
  animation: rotate 2s linear infinite;
  position: absolute;
  top: 50%;
  left: 50%;
}

.spinner .path {
  stroke: #f7f7f7;
  stroke-linecap: round;
  animation: dash 1.5s ease-in-out infinite;
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes dash {
  0% {
    stroke-dasharray: 1 150;
    stroke-dashoffset: 0;
  }

  50% {
    stroke-dasharray: 90 150;
    stroke-dashoffset: -35px;
  }

  100% {
    stroke-dasharray: 90 150;
    stroke-dashoffset: -124px;
  }
}

.checkmark__circle {
  stroke-dasharray: 166;
  stroke-dashoffset: 166px;
  stroke-width: 4px;
  stroke-miterlimit: 10;
  stroke: #f7f7f7;
  fill: none;
  animation: stroke .6s cubic-bezier(.65, 0, .45, 1) forwards;
}

.checkmark {
  width: 56px;
  height: 56px;
  stroke-width: 4px;
  stroke: #f7f7f7;
  stroke-miterlimit: 10;
  border-radius: 50%;
  margin: 10% auto;
  animation: scale .3s ease-in-out .9s both;
  display: block;
}

.checkmark__check {
  transform-origin: 50%;
  stroke-dasharray: 48;
  stroke-dashoffset: 48px;
  animation: stroke .3s cubic-bezier(.65, 0, .45, 1) .8s forwards;
}

.checkmark__cross {
  stroke: #b92f2c;
  fill: #b92f2c;
}

@keyframes stroke {
  100% {
    stroke-dashoffset: 0;
  }
}

@keyframes scale {
  0%, 100% {
    transform: none;
  }

  50% {
    transform: scale3d(1.1, 1.1, 1);
  }
}

